import React, { useEffect, useRef } from 'react';
import { gsap } from "gsap";
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import './main.css'
import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation, Autoplay } from 'swiper/modules';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/autoplay';
import { Link } from 'react-router-dom';
import Section01 from './Section/Section01';
import Section02 from './Section/Section02';
import Section03 from './Section/Section03';
import Section04 from './Section/Section04';
import Section05 from './Section/Section05';
import Section06 from './Section/Section06';
import Section07 from './Section/Section07';
import Section08 from './Section/Section08';

gsap.registerPlugin(ScrollTrigger);

const Main = () => {  
  const firstboxRef = useRef(null);
  const secondboxRef = useRef(null);
  const thirdboxRef = useRef(null);
  const visualCopyxRef = useRef(null);
  const swiperxRef = useRef(null);

  useEffect(() => {
    const letters = firstboxRef.current.querySelectorAll('.first-text span');
    const seconletters = secondboxRef.current.querySelectorAll('.second-text span');
    const thirdletters = thirdboxRef.current.querySelectorAll('.third-text span');
    const visualcopy = visualCopyxRef.current;
    const swiperiew = swiperxRef.current

    const tl = gsap.timeline({ defaults: { ease: 'power2.out'} });
    const t2 = gsap.timeline({ defaults: { ease: 'power2.out' } });
    const t3 = gsap.timeline({ defaults: { ease: 'power2.out' } });
    const t4 = gsap.timeline({ defaults: { ease: 'power2.out' } });
    const t5 = gsap.timeline({ defaults: { ease: 'power2.out' } });

    //visual text motion    
    setTimeout(() => {
      tl.fromTo(
        letters,
        { y: 150, opacity: 0 },
        {
          duration: 0.6,
          y: 0,
          opacity: 1,
          stagger: 0.35,
          scrub: 3,
          onComplete: () => {
            gsap.to(letters, {
              duration: 0.75,
              y: 0,
              opacity: 1,
              scrub: 3,
              stagger: 0.35,
              ease: 'power2.out',
            });
          },
        }
      );
    },400)

    setTimeout(() => {
      t2.fromTo(
        seconletters,
        { x: -1000, scaleX: 0, opacity: 0 },
        {
          duration: 1.05,
          x: 0,
          scaleX: 1,
          opacity: 1,
          stagger: { each: 0.06 },
          scrub: 3,
          onComplete: () => {
            gsap.to(letters, {
              duration: 1.05,
              x: 0,
              scaleX: 1,
              opacity: 1,
              scrub: 3,
              stagger: { each: 0.06 },
              ease: 'power2.out',
            });
          },
        }
      );
    }, 2000)

    setTimeout(() => {
      t3.fromTo(
        thirdletters,
        { scaleX: 580, opacity: 0 },
        {
          duration: 0.45,
          scaleX: 1,
          opacity: 1,
          stagger: { each: 0.1 },
          scrub: 4,          
          onComplete: () => {
            gsap.to(letters, {
              duration: 0.45,
              scaleX: 1,
              opacity: 1,
              stagger: { each: 0.1 },
              ease: 'power2.out',
            });
          },
        }
      );
    }, 2800);

    setTimeout(() => {
      t4.fromTo(
        visualcopy,
        { y: 150, opacity: 0 },
        {
          duration: 0.6,
          y: 0,
          opacity: 1,
          stagger: 1,
          scrub: 3,
          onComplete: () => {
            gsap.to(visualcopy, {
              duration: 0.75,
              y: 0,
              opacity: 1,
              scrub: 3,
              stagger: 0.35,
              ease: 'power2.out',
            });
          },
        }
      );
    }, 500)
    setTimeout(() => {
      t5.fromTo(
        swiperiew,
        { y: 150, opacity: 0 },
        {
          duration: 0.9,
          y: 0,
          opacity: 1,
          stagger: 1,
          scrub: 3,
          onComplete: () => {
            gsap.to(swiperiew, {
              duration:1,
              y: 0,
              opacity: 1,
              scrub: 3,
              stagger: 0.35,
              ease: 'power2.out',
            });
          },
        }
      );
    }, 3500)

    return () => {
      ScrollTrigger.getAll().forEach((trigger) => {
        trigger.kill();
      }); 
    };
    
  }, []);

  return (
    <>
      <div className='visual-box'>
        <div className="visual-text-box" ref={swiperxRef}>
          <div className="first-text" ref={firstboxRef}>
            <span>J</span><span>U</span><span>S</span><span>T</span>
          </div>
          <div className="second-text" ref={secondboxRef}>
            <span>D</span><span>O</span>
          </div>
          <div className="third-text" ref={thirdboxRef}>
            <span>I</span><span>T</span>
          </div>
        </div>
        <div className="visual-swiper-box" ref={swiperxRef}>
          <div className="visual-swiper">
            <div className="swiper-left">
              <h3>PRODUCT</h3>
              <div className='banner-copy'>
                Adds savory flavor to the savory taste<br/>
                potatoes Original dough Crunch rice preserves 
              </div>
              <div className="swiper-btnbox">
                <div className="slider_prev"><span className="blind">Prev</span></div>
                <div className="slider_next"><span className="blind">Next</span></div>
              </div>
            </div>
            <div className="swiper-rigt">
              <div className="main-banner-swiper">
                <Swiper
                  slidesPerView={4}
                  navigation={{
                    nextEl: ".slider_next",
                    prevEl: ".slider_prev",
                  }}
                  modules={[Navigation, Autoplay]}
                  autoplay={{ delay: 3500, disableOnInteraction: false }}
                  className="mySwiper">
                  <SwiperSlide>
                    <figure>
                      <Link to={'/'} style={{ backgroundImage: `url(${require('../../assets/images/main/main-banner01.png')})`}}></Link>
                    </figure>
                  </SwiperSlide>
                  <SwiperSlide>
                    <figure>
                      <Link to={'/'} style={{ backgroundImage: `url(${require('../../assets/images/main/main-banner02.png')})`}}></Link>
                    </figure>
                  </SwiperSlide>
                  <SwiperSlide>
                    <figure>
                      <Link to={'/'} style={{ backgroundImage: `url(${require('../../assets/images/main/main-banner03.png')})`}}></Link>
                    </figure>
                  </SwiperSlide>
                  <SwiperSlide>
                    <figure>
                      <Link to={'/'} style={{ backgroundImage: `url(${require('../../assets/images/main/main-banner04.png')})`}}></Link>
                    </figure>
                  </SwiperSlide>
                  <SwiperSlide>
                    <figure>
                      <Link to={'/'} style={{ backgroundImage: `url(${require('../../assets/images/main/main-banner01.png')})`}}></Link>
                    </figure>
                  </SwiperSlide>
                  <SwiperSlide>
                    <figure>
                      <Link to={'/'} style={{ backgroundImage: `url(${require('../../assets/images/main/main-banner02.png')})`}}></Link>
                    </figure>
                  </SwiperSlide>
                  <SwiperSlide>
                    <figure>
                      <Link to={'/'} style={{ backgroundImage: `url(${require('../../assets/images/main/main-banner03.png')})` }}></Link>
                    </figure>
                  </SwiperSlide>
                </Swiper>
              </div>
            </div>
          </div>
        </div>
        <div className='visual-img' style={{ backgroundImage: `url(${require('../../assets/images/main/main-visual-bg.png')})` }}>
        </div>
      </div>
      <Section01 />
      <Section02 />
      <Section03 />
      <Section04 />
      <Section05 />
      <Section06 />
      <Section07 />
      <Section08 />
    </>
  );
};

export default Main;