import React, { useEffect, useRef } from 'react';
import { gsap } from "gsap";
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Pagination, Autoplay } from 'swiper/modules';
import { Link } from 'react-router-dom';
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/autoplay';
import './section08.css';
gsap.registerPlugin(ScrollTrigger);

const Section08 = () => {
    const copyboxRef = useRef(null);
    const shopSportboxRef = useRef(null);

    useEffect(() => {
        const copy = copyboxRef.current;
        const shop = shopSportboxRef.current
        const tl = gsap.timeline({ defaults: { ease: 'power2.out' } });
        const t2 = gsap.timeline({ defaults: { ease: 'power2.out' } });

        tl.fromTo(
            shop,
            { autoAlpha: 0, y: 250, transformOrigin: 'center' },
            {
                autoAlpha: 1,
                y: 0,
                duration: 0.3,
                ease: 'power2.out',
                scrollTrigger: {
                    trigger: shop,
                    start: "+=20 100%",
                    end: "+=20 60%",
                    scrub: 2,
                    invalidateOnRefresh: true,
                    // markers: {
                    //     startColor: '#ffd632',
                    //     endColor: 'black',
                    //     fontSize: '2rem',
                    //     indent: 200
                    // }
                },
            }
        );
        t2.fromTo(
            copy,
            { autoAlpha: 0, y: 150, transformOrigin: 'center' },
            {
                autoAlpha: 1,
                y: 0,
                duration: 0.25,
                ease: 'power2.out',
                scrollTrigger: {
                    trigger: copy,
                    start: "+=20 95%",
                    end: "+=20 60%",
                    scrub: 2,
                    invalidateOnRefresh: true,
                    // markers: {
                    //     startColor: '#ffd632',
                    //     endColor: 'black',
                    //     fontSize: '2rem',
                    //     indent: 200
                    // }
                },
            }
        );
    }, [])
    return (
        <section className='section1'>
            <div className='inner'>
                <div className="con-title" ref={copyboxRef}>
                    <h2>SHOP<br />SPORT</h2>
                    <p>
                        To make the warm gift even more impressive.
                    </p>
                </div>
            </div>
            <div className='inner'>
                <div className="shop-swiper" ref={shopSportboxRef}>
                <Swiper
                    slidesPerView={3}
                    spaceBetween={20}
                    pagination={{
                    el: '.shop-swiper-pagination',
                    type: 'progressbar',
                    }}
                    modules={[Pagination, Autoplay]}
                    autoplay={{ delay: 5500, disableOnInteraction: false }}
                >
                    <SwiperSlide>
                    <Link to={'/'}>
                        <div className="shop-box">
                        <div className="shop-img">
                            <div className="img-box" style={{ backgroundImage: `url(${require('../../../assets/images/main/sport-img01.png')})` }}></div>
                        </div>
                        <div className="shop-info">
                            <strong>나이키 골프</strong>
                            <i>골프 필드 위 자신감을 더해주는 스타일</i>
                            <div className='btn-box'>
                            <button className='btn-frame black-line'>Buy Now</button>
                            </div>
                        </div>
                        </div>
                    </Link>
                    </SwiperSlide>
                    <SwiperSlide>
                    <Link to={'/'}>
                        <div className="shop-box">
                        <div className="shop-img">
                            <div className="img-box" style={{ backgroundImage: `url(${require('../../../assets/images/main/sport-img02.png')})` }}></div>
                        </div>
                        <div className="shop-info">
                            <strong>나이키 트레일 농구</strong>
                            <i>새로운 모험으로 이끌어 주는 아이템</i>
                            <div className='btn-box'>
                            <button className='btn-frame black-line'>Buy Now</button>
                            </div>
                        </div>
                        </div>
                    </Link>
                    </SwiperSlide>
                    <SwiperSlide>
                    <Link to={'/'}>
                        <div className="shop-box">
                        <div className="shop-img">
                            <div className="img-box" style={{ backgroundImage: `url(${require('../../../assets/images/main/sport-img03.png')})` }}></div>
                        </div>
                        <div className="shop-info">
                            <strong>나이키 테니스</strong>
                            <i>강력한 서브를 돋보이게 하는 스타일</i>
                            <div className='btn-box'>
                            <button className='btn-frame black-line'>Buy Now</button>
                            </div>
                        </div>
                        </div>
                    </Link>
                    </SwiperSlide>
                    <SwiperSlide>
                    <Link to={'/'}>
                        <div className="shop-box">
                        <div className="shop-img">
                            <div className="img-box" style={{ backgroundImage: `url(${require('../../../assets/images/main/sport-img01.png')})` }}></div>
                        </div>
                        <div className="shop-info">
                            <strong>나이키 골프</strong>
                            <i>골프 필드 위 자신감을 더해주는 스타일</i>
                            <div className='btn-box'>
                            <button className='btn-frame black-line'>Buy Now</button>
                            </div>
                        </div>
                        </div>
                    </Link>
                    </SwiperSlide>
                    <SwiperSlide>
                    <Link to={'/'}>
                        <div className="shop-box">
                        <div className="shop-img">
                            <div className="img-box" style={{ backgroundImage: `url(${require('../../../assets/images/main/sport-img03.png')})` }}></div>
                        </div>
                        <div className="shop-info">
                            <strong>나이키 골프</strong>
                            <i>골프 필드 위 자신감을 더해주는 스타일</i>
                            <div className='btn-box'>
                            <button className='btn-frame black-line'>Buy Now</button>
                            </div>
                        </div>
                        </div>
                    </Link>
                    </SwiperSlide>
                    <SwiperSlide>
                    <Link to={'/'}>
                        <div className="shop-box">
                        <div className="shop-img">
                            <div className="img-box" style={{ backgroundImage: `url(${require('../../../assets/images/main/sport-img02.png')})` }}></div>
                        </div>
                        <div className="shop-info">
                            <strong>나이키 골프</strong>
                            <i>골프 필드 위 자신감을 더해주는 스타일</i>
                            <div className='btn-box'>
                            <button className='btn-frame black-line'>Buy Now</button>
                            </div>
                        </div>
                        </div>
                    </Link>
                    </SwiperSlide>
                </Swiper>
                <div className="pagnation-box">
                    <div className='shop-swiper-pagination'></div>
                </div>
                </div>
            </div>
        </section>
    );
};

export default Section08;