import React from 'react';
import './sub.css'
import img01 from '../../assets/images/sub/sub-img01.png'
import img02 from '../../assets/images/sub/sub-img02.png'
import img03 from '../../assets/images/sub/sub-img03.png'
import img04 from '../../assets/images/sub/sub-img04.png'
import img05 from '../../assets/images/sub/sub-img05.png'
import img06 from '../../assets/images/sub/sub-img06.png'
import img07 from '../../assets/images/sub/sub-img07.png'
import img08 from '../../assets/images/sub/sub-img08.png'
import img09 from '../../assets/images/sub/sub-img09.png'
import img10 from '../../assets/images/sub/sub-img10.png'
import img11 from '../../assets/images/sub/sub-img11.png'
import img12 from '../../assets/images/sub/sub-img12.png'
import star from '../../assets/images/sub/star.svg'
import cart from '../../assets/images/sub/cart.svg'

const ProductManIndex = () => {
  const imgArr = [img01, img02, img03, img04, img05, img06, img07, img08, img09, img10, img11, img12]
  return (
    <div className='sub-inner'>
      <div className='sub-title-wrap'>
        <h2 className='popins'>MEN</h2>
        <p>자신만의 아름다움을 찾아가는 여정에 참여하세요.</p>
      </div>
      <div className='card-wrapper'>
        <div className='card-inner'>
          {imgArr.map((item) => (
            <div className='card-box'>
            <div className='card-img'>
              <img src={item} alt=''/>
            </div>
            <div className='card-infor'>
              <div className='card-title'>미라클 토닝글로우 세럼 50ml</div>
              <div className='card-txt'>코어탄력키트, 앰플레이어링, 토탈안티에이징</div>
              <div className='card-price'><span>10% </span>8,000원 <i>7,600원</i></div>
              <div className='card-star'>
                <i><img src={star} alt=''/></i>
                <i><img src={star} alt=''/></i>
                <i><img src={star} alt=''/></i>
                <i><img src={star} alt=''/></i>
                <i className='score'>4.0 (232)</i>
                <i className='badge-gift'>gift</i>
                <i className='badge-cupon'>cupon</i>
              </div>
              <div className='cart-btn'>
                <img src={cart} alt=''/>
              </div>
            </div>
          </div>  
          ))}
        </div>
      </div>
    </div>
  );
};

export default ProductManIndex;