import Layout from '../Layout';
import Main from '../pages/Main/Main';
import New from '../pages/sub/New';
import ProductManIndex from '../pages/sub/ProductManIndex';
import ProductMenu from '../pages/sub/ProductMenu';
import ProductWomenIndex from '../pages/sub/ProductWomenIndex';



export const RouterInfo = [
  {
    path: "/",
    element: <Layout />,
    children: [
      {
        index: true,
        element: <Main />,
        name: 'main'
      },
      {
        path: '/menu',
        element: <ProductMenu />,
        children: [
          // {
          //   index: true,
          //   element: <ProductManIndex/>,
          //   name:'ProductManIndex'
          // },
          {
            path: 'man',
            element: <ProductManIndex />,
            name: 'man'
          },
          {
            path: 'man/new',
            element: <New/>,
            name: 'New'
          },
          {
            path: 'woman',
            element: <ProductWomenIndex />,
            name: 'woman'
          }
        ]
      },
    ]
  }
];
