import React, { useEffect, useRef, useState } from 'react';
import { gsap } from "gsap";
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import './section03.css'

gsap.registerPlugin(ScrollTrigger);

const Section03 = () => {
    const [hoveredGift, setHoveredGift] = useState(null)
    const memBerboxRef = useRef(null);
    const copyboxRef = useRef(null);

    const handleMouseEnter = (gift) => {
        setHoveredGift(gift);
    };
    const handleMouseLeave = () => {
        setHoveredGift(null);
    };

    useEffect(() => {
        const menber = memBerboxRef.current;
        const copy = copyboxRef.current;
        const tl = gsap.timeline({ defaults: { ease: 'power2.out' } });
        const t2 = gsap.timeline({ defaults: { ease: 'power2.out' } });

        tl.fromTo(
            menber,
            { autoAlpha: 0, y: 250, transformOrigin: 'center' },
            {
                autoAlpha: 1,
                y: 0,
                duration: 0.3,
                ease: 'power2.out',
                scrollTrigger: {
                    trigger: menber,
                    start: "+=20 100%",
                    end: "+=20 60%",
                    scrub: 2,
                    invalidateOnRefresh: true,
                    // markers: {
                    //     startColor: '#ffd632',
                    //     endColor: 'black',
                    //     fontSize: '2rem',
                    //     indent: 200
                    // }
                },
            }
        );
        t2.fromTo(
            copy,
            { autoAlpha: 0, y: 150, transformOrigin: 'center' },
            {
                autoAlpha: 1,
                y: 0,
                duration: 0.25,
                ease: 'power2.out',
                scrollTrigger: {
                    trigger: copy,
                    start: "+=20 95%",
                    end: "+=20 60%",
                    scrub: 2,
                    invalidateOnRefresh: true,
                    // markers: {
                    //     startColor: '#ffd632',
                    //     endColor: 'black',
                    //     fontSize: '2rem',
                    //     indent: 200
                    // }
                },
            }
        );
    }, [])

    return(
        <section className='section1'>
            <div className='inner'>
                <div className="con-title" ref={copyboxRef}>
                    <h2>member<br />gift</h2>
                    <p>
                        To make the warm gift even more impressive.
                    </p>
                </div>
            </div>
            <div className='inner sec03-inner'>
                <div className='sec03-box' data-img={hoveredGift || 'bg-image_01'} ref={memBerboxRef}>
                    <div className='member-box' onMouseEnter={() => handleMouseEnter('bg-image_01')} onMouseLeave={handleMouseLeave}>
                        <div className='member-title en'>HEART</div>
                        <div className='member-txt popins'>추운 겨울, 소중한 이들에게 따뜻함을 선물해 보세요</div>
                        <div className='more-btn-wrap'>
                            <button className='btn-frame white-line'>Detail View</button>
                        </div>
                    </div>
                    <div className='member-box' onMouseEnter={() => handleMouseEnter('bg-image_02')} onMouseLeave={handleMouseLeave}>
                        <div className='member-title en'>SPORT</div>
                        <div className='member-txt popins'>추운 겨울, 소중한 이들에게 따뜻함을 선물해 보세요</div>
                        <div className='more-btn-wrap'>
                            <button className='btn-frame white-line'>Detail View</button>
                        </div>
                    </div>
                    <div className='member-box' onMouseEnter={() => handleMouseEnter('bg-image_03')} onMouseLeave={handleMouseLeave}>
                        <div className='member-title en'>CHILDREN</div>
                        <div className='member-txt popins'>추운 겨울, 소중한 이들에게 따뜻함을 선물해 보세요</div>
                        <div className='more-btn-wrap'>
                            <button className='btn-frame white-line'>Detail View</button>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default Section03;