import React from 'react';
import './footer.css';
import { Link } from 'react-router-dom';

const Footer = () => {
  return (
    <footer>
      <div className='footer-inner'>
        <Link to={'/'} className='foot-logo'><span className='blind'>나이키</span></Link>
        <div className='co-info'>
          <ul className="link-info">
            <li><Link to={'/'}>사업자정보 확인</Link></li>
            <li><Link to={'/'}>개인정보 처리방침</Link></li>
            <li><Link to={'/'}>이용약관</Link></li>
          </ul>
          <address>
            <span>대표<i>:</i>홍길동</span><span>사업자등록번호<i>:</i>000-22-66666</span><span>주소<i>:</i>서울특별시 서울구 서울로 NIKE빌딩</span> <span>통신판매업 신고<i>:</i>2023-서울당산-0000</span>
          </address>
          <div className="copyright">
            Copyright © Nike. All rights reserved
          </div>          
        </div> 
        <div className='sns-link'>
          <Link to={'/'}><img src={require('../../assets/images/common/icon-instagram.svg').default} alt="instgram" /><span className='blind'>instagram</span></Link>
          <Link to={'/'}><img src={require('../../assets/images/common/icon-twiter.svg').default} alt="twiter" /><span className='blind'>twiter</span></Link>
          <Link to={'/'}><img src={require('../../assets/images/common/icon-youtube.svg').default} alt="Youtube" /><span className='blind'>Youtube</span></Link>
        </div>
      </div> 
    </footer>
  );
};

export default Footer;