import React, { useEffect, useRef } from 'react';
import { gsap } from "gsap";
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import './section02.css'
import style01 from '../../../assets/images/section/sec02-img01.png'
import style02 from '../../../assets/images/section/sec02-img02.png'
gsap.registerPlugin(ScrollTrigger);


const Section02 = () => {   
    const ref = useRef();
    ref.current = [];
    const copyboxRef = useRef(null);

    useEffect(() => {
        const tl = gsap.timeline({ defaults: { ease: 'power2.out' } });
        const copy = copyboxRef.current;

        tl.fromTo(
            copy,
            { autoAlpha: 0, y: 150, transformOrigin: 'center' },
            {
                autoAlpha: 1,
                y: 0,
                duration: 0.25,
                ease: 'power2.out',
                scrollTrigger: {
                    trigger: copy,
                    start: "+=20 95%",
                    end: "+=20 60%",
                    scrub: 2,
                    invalidateOnRefresh: true,
                    // markers: {
                    //     startColor: '#ffd632',
                    //     endColor: 'black',
                    //     fontSize: '2rem',
                    //     indent: 200
                    // }
                },
            }
        );
        //content NEW STYLE motion
        ref.current.forEach((item) => {
            const { element, direction } = item;
            if (direction === 'top') {
                setTimeout(() => {
                    gsap.fromTo(
                        element,
                        { autoAlpha: 0, y: -700, transformOrigin: 'top' },
                        {
                            autoAlpha: 1,
                            y: 0,
                            duration: 0.5,
                            ease: 'power2.out',
                            scrollTrigger: {
                                trigger: element,
                                start: "+=20 95%",
                                end: "+=20 55%",
                                scrub: 3,
                                invalidateOnRefresh: true,
                                // markers: {
                                //     startColor: '#328eff',
                                //     endColor: 'black',
                                //     fontSize: '2rem',
                                //     indent: 200
                                // }
                            },
                        }
                    );
                }, 200)
            } else if (direction === 'bottom') {
                gsap.fromTo(
                    element,
                    { autoAlpha: 0, y: 700, transformOrigin: 'top' },
                    {
                        autoAlpha: 1,
                        y: 0,
                        duration: 0.75,
                        ease: 'power2.out',
                        scrollTrigger: {
                            trigger: element,
                            start: "+=20 95%",
                            end: "+=20 50%",
                            scrub: 3,                            
                            invalidateOnRefresh: true,
                            // markers: {
                            //     startColor: 'blue',
                            //     endColor: 'black',
                            //     fontSize: '2rem',
                            //     indent: 200
                            // }
                        },
                    }
                );
            }
        });
    }, [])
    
    const addtoRefs = (el, direction) => {
        if (el && !ref.current.some((item) => item.element === el)) {
            ref.current.push({ element: el, direction });
        }
    };

    return(
        <section className='section1'>
            <div className='inner'>
                <div className="con-title" ref={copyboxRef}>
                    <h2>NEW<br />STYLE</h2>
                    <p>To make the warm gift even more impressive.</p>
                </div>
            </div>
            <div className='sec02-flex-wrap'>
                <div className='new-style-inner' ref={(el) => addtoRefs(el, 'top')}>
                    <div className='new-style-bx' style={{backgroundImage: `url(${style01})`}}>
                        <div className='new-style-text-wrap'>
                            <h3 className='new-style-title en'>New style </h3>
                            <div className='new-style-sub01'>댄서 허니제이가 추천하는 스타일</div>
                            <div className='new-style-sub02'>자신을 표현하는허니제이의 스타일에서 선물의 영감을 발견해 보세요.</div>
                            <div className='detail_btn-wrap'>
                                <button className='btn-frame white-line'>Detail View</button>
                            </div>
                        </div>
                    </div>
                </div>
                <div className='new-style-inner' ref={(el) => addtoRefs(el, 'bottom')}>
                    <div className='new-style-bx' style={{backgroundImage: `url(${style02})`}}>
                        <div className='new-style-text-wrap'>
                            <h3 className='new-style-title en'>New style </h3>
                            <div className='new-style-sub01'>트레이너 홍찬주가 추천하는 스타일</div>
                            <div className='new-style-sub02'>홍찬주 트레이너의 스타일에서 선물의 영감을 받아 보세요.</div>
                            <div className='detail_btn-wrap'>
                                <button className='btn-frame white-line'>Detail View</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default Section02