import React, { useEffect, useRef } from 'react';
import { gsap } from "gsap";
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Pagination, Autoplay } from 'swiper/modules';
import { Link } from 'react-router-dom';
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/autoplay';
import './section05.css';
gsap.registerPlugin(ScrollTrigger);

const Section05 = () => {
    const copyboxRef = useRef(null);
    const alwaysIconIcboxRef = useRef(null);

    useEffect(() => {
        const copy = copyboxRef.current;
        const always = alwaysIconIcboxRef.current
        const tl = gsap.timeline({ defaults: { ease: 'power2.out' } });
        const t2 = gsap.timeline({ defaults: { ease: 'power2.out' } });


        tl.fromTo(
            always,
            { autoAlpha: 0, y: 250, transformOrigin: 'center' },
            {
                autoAlpha: 1,
                y: 0,
                duration: 0.3,
                ease: 'power2.out',
                scrollTrigger: {
                    trigger: always,
                    start: "+=20 100%",
                    end: "+=20 60%",
                    scrub: 2,
                    invalidateOnRefresh: true,
                    // markers: {
                    //     startColor: '#ffd632',
                    //     endColor: 'black',
                    //     fontSize: '2rem',
                    //     indent: 200
                    // }
                },
            }
        );
        t2.fromTo(
            copy,
            { autoAlpha: 0, y: 150, transformOrigin: 'center' },
            {
                autoAlpha: 1,
                y: 0,
                duration: 0.25,
                ease: 'power2.out',
                scrollTrigger: {
                    trigger: copy,
                    start: "+=20 95%",
                    end: "+=20 60%",
                    scrub: 2,
                    invalidateOnRefresh: true,
                    // markers: {
                    //     startColor: '#ffd632',
                    //     endColor: 'black',
                    //     fontSize: '2rem',
                    //     indent: 200
                    // }
                },
            }
        );
    }, [])

    return (
        <section className='section1'>
            <div className='inner'>
                <div className="con-title" ref={copyboxRef}>
                    <h2>ALWAYS<br />ICONIC</h2>
                    <p>
                        To make the warm gift even more impressive.
                    </p>
                </div>
            </div>
            <div className='inner'>
                <div className="allways-swiper" ref={alwaysIconIcboxRef}>
                    <Swiper
                        slidesPerView={4}
                        spaceBetween={1}
                        pagination={{
                            el: '.allways-swiper-pagination',
                            type: 'progressbar',
                        }}
                        modules={[Pagination, Autoplay]}
                        autoplay={{ delay: 4500, disableOnInteraction: false }}
                    >
                        <SwiperSlide>
                            <Link to={'/'}>
                                <div className="allways-img">
                                    <div className="always-info">
                                        <span><em>Best</em></span>
                                        <i>Air Pos</i>
                                    </div>
                                    <div className="img-box" style={{ backgroundImage: `url(${require('../../../assets/images/main/allawys-img01.svg').default})` }}></div>
                                </div>
                            </Link>
                        </SwiperSlide>
                        <SwiperSlide>
                            <Link to={'/'}>
                                <div className="allways-img">
                                    <div className="always-info">
                                        <span><em>New</em></span>
                                        <i>Air Max</i>
                                    </div>
                                    <div className="img-box" style={{ backgroundImage: `url(${require('../../../assets/images/main/allawys-img02.svg').default})` }}></div>
                                </div>
                            </Link>
                        </SwiperSlide>
                        <SwiperSlide>
                            <Link to={'/'}>
                                <div className="allways-img">
                                    <div className="always-info">
                                        <span><em>Best</em></span>
                                        <i>Air Pos</i>
                                    </div>
                                    <div className="img-box" style={{ backgroundImage: `url(${require('../../../assets/images/main/allawys-img03.svg').default})` }}></div>
                                </div>
                            </Link>
                        </SwiperSlide>
                        <SwiperSlide>
                            <Link to={'/'}>
                                <div className="allways-img">
                                    <div className="always-info">
                                        <span><em>Best</em></span>
                                        <i>Air Pos</i>
                                    </div>
                                    <div className="img-box" style={{ backgroundImage: `url(${require('../../../assets/images/main/allawys-img04.svg').default})` }}></div>
                                </div>
                            </Link>
                        </SwiperSlide>
                        <SwiperSlide>
                            <Link to={'/'}>
                                <div className="allways-img">
                                    <div className="always-info">
                                        <span><em>Best</em></span>
                                        <i>Air Pos</i>
                                    </div>
                                    <div className="img-box" style={{ backgroundImage: `url(${require('../../../assets/images/main/allawys-img01.svg').default})` }}></div>
                                </div>
                            </Link>
                        </SwiperSlide>
                        <SwiperSlide>
                            <Link to={'/'}>
                                <div className="allways-img">
                                    <div className="always-info">
                                        <span><em>Best</em></span>
                                        <i>Air Pos</i>
                                    </div>
                                    <div className="img-box" style={{ backgroundImage: `url(${require('../../../assets/images/main/allawys-img03.svg').default})` }}></div>
                                </div>
                            </Link>
                        </SwiperSlide>
                    </Swiper>
                    <div className="pagnation-box">
                        <div className='allways-swiper-pagination'></div>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default Section05;