import React, { useEffect, useRef, useState } from 'react';
import { gsap } from "gsap";
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import './section07.css'
gsap.registerPlugin(ScrollTrigger);

const Section07 = () => {
    const copyboxRef = useRef(null);
    const styleViewboxRef = useRef(null);
    const [hoveredGift, setHoveredGift] = useState(null)

    const handleMouseEnter = (gift) => {
    setHoveredGift(gift);
    };
    const handleMouseLeave = () => {
    setHoveredGift(null);
    };

    useEffect(() => {
        const copy = copyboxRef.current;
        const styleview = styleViewboxRef.current
        const tl = gsap.timeline({ defaults: { ease: 'power2.out' } });
        const t2 = gsap.timeline({ defaults: { ease: 'power2.out' } });

        tl.fromTo(
            styleview,
            { autoAlpha: 0, y: 250, transformOrigin: 'center' },
            {
                autoAlpha: 1,
                y: 0,
                duration: 0.3,
                ease: 'power2.out',
                scrollTrigger: {
                    trigger: styleview,
                    start: "+=20 100%",
                    end: "+=20 60%",
                    scrub: 2,
                    invalidateOnRefresh: true,
                    // markers: {
                    //     startColor: '#ffd632',
                    //     endColor: 'black',
                    //     fontSize: '2rem',
                    //     indent: 200
                    // }
                },
            }
        );
        t2.fromTo(
            copy,
            { autoAlpha: 0, y: 150, transformOrigin: 'center' },
            {
                autoAlpha: 1,
                y: 0,
                duration: 0.25,
                ease: 'power2.out',
                scrollTrigger: {
                    trigger: copy,
                    start: "+=20 95%",
                    end: "+=20 60%",
                    scrub: 2,
                    invalidateOnRefresh: true,
                    // markers: {
                    //     startColor: '#ffd632',
                    //     endColor: 'black',
                    //     fontSize: '2rem',
                    //     indent: 200
                    // }
                },
            }
        );
    }, [])

    return (
        <section className='section1'>
            <div className='inner'>
                <div className="con-title" ref={copyboxRef}>
                    <h2>STYLING<br />VIEW</h2>
                    <p>
                        To make the warm gift even more impressive.
                    </p>
                </div>
            </div>
            <div className='inner sec07-inner' ref={styleViewboxRef}>
                <div className='sec07-box' data-img={hoveredGift || 'bg-image_01'}>
                    <div className='view-box' onMouseEnter={() => handleMouseEnter('bg-image_01')} onMouseLeave={handleMouseLeave}>
                        <div className='view-title popins'>V2K 스타일링</div>
                        <div className='buy-btn-wrap'>
                            <button className='buy en'>Buy Now</button>
                        </div>
                    </div>
                    <div className='view-box' onMouseEnter={() => handleMouseEnter('bg-image_02')} onMouseLeave={handleMouseLeave}>
                        <div className='view-title popins'>에어맥스 스타일링</div>
                        <div className='buy-btn-wrap'>
                            <button className='buy en'>Buy Now</button>
                        </div>
                    </div>
                    <div className='view-box' onMouseEnter={() => handleMouseEnter('bg-image_03')} onMouseLeave={handleMouseLeave}>
                        <div className='view-title popins'>보메로 스타일링</div>
                        <div className='buy-btn-wrap'>
                            <button className='buy en'>Buy Now</button>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default Section07;