import React, { useEffect, useRef } from 'react';
import { gsap } from "gsap";
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import './section06.css';
gsap.registerPlugin(ScrollTrigger);

const Section06 = () => {
    const copyboxRef = useRef(null);
    const trendNowboxRef = useRef(null);

    useEffect(() => {
        const copy = copyboxRef.current;
        const trend = trendNowboxRef.current
        const tl = gsap.timeline({ defaults: { ease: 'power2.out' } });
        const t2 = gsap.timeline({ defaults: { ease: 'power2.out' } });

        tl.fromTo(
            trend,
            { autoAlpha: 0, y: 250, transformOrigin: 'center' },
            {
                autoAlpha: 1,
                y: 0,
                duration: 0.3,
                ease: 'power2.out',
                scrollTrigger: {
                    trigger: trend,
                    start: "+=20 100%",
                    end: "+=20 60%",
                    scrub: 2,
                    invalidateOnRefresh: true,
                    // markers: {
                    //     startColor: '#ffd632',
                    //     endColor: 'black',
                    //     fontSize: '2rem',
                    //     indent: 200
                    // }
                },
            }
        );
        t2.fromTo(
            copy,
            { autoAlpha: 0, y: 150, transformOrigin: 'center' },
            {
                autoAlpha: 1,
                y: 0,
                duration: 0.25,
                ease: 'power2.out',
                scrollTrigger: {
                    trigger: copy,
                    start: "+=20 95%",
                    end: "+=20 60%",
                    scrub: 2,
                    invalidateOnRefresh: true,
                    // markers: {
                    //     startColor: '#ffd632',
                    //     endColor: 'black',
                    //     fontSize: '2rem',
                    //     indent: 200
                    // }
                },
            }
        );
    }, [])

    return(
        <section className='section1'>
            <div className='inner'>
                <div className="con-title" ref={copyboxRef}>
                    <h2>trending<br />now</h2>
                    <p>To make the warm gift even more impressive.</p> 
                </div>
            </div>
            <div className='inner'>
                <div className="sec06-grid-wrap" ref={trendNowboxRef}>
                    <div className="gird-item01">
                        <div className='gird-item-bg gird-big' style={{ backgroundImage: `url(${require('../../../assets/images/section/sec06-img01.png')})` }}>
                            <div className='grid-infor'>
                                <div className='grid-name popins'>상상 그 이상의 스피드</div>
                                <div className='buy-btn-wrap'>
                                    <button className='btn-frame white-line'>Buy Now</button>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className='grid-item02'>
                        <div className='gird-item-bg gird-small' style={{ backgroundImage: `url(${require('../../../assets/images/section/sec06-img02.png')})` }}>
                            <div className='grid-infor'>
                                <div className='grid-name popins'>나이키 덩크</div>
                                <div className='buy-btn-wrap'>
                                    <button className='btn-frame white-line'>Buy Now</button>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className='grid-item03'>
                        <div className='gird-item-bg gird-small' style={{ backgroundImage: `url(${require('../../../assets/images/section/sec06-img03.png')})` }}>
                            <div className='grid-infor'>
                                <div className='grid-name popins'>에어맥스 펄스 롬</div>
                                <div className='buy-btn-wrap'>
                                    <button className='btn-frame white-line'>Buy Now</button>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className='grid-item04'>
                        <div className='gird-item-bg gird-small' style={{ backgroundImage: `url(${require('../../../assets/images/section/sec06-img04.png')})` }}>
                            <div className='grid-infor'>
                                <div className='grid-name popins'>에어맥스</div>
                                <div className='buy-btn-wrap'>
                                    <button className='btn-frame white-line'>Buy Now</button>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className='grid-item05'>
                        <div className='gird-item-bg gird-small' style={{ backgroundImage: `url(${require('../../../assets/images/section/sec06-img05.png')})` }}>
                            <div className='grid-infor'>
                                <div className='grid-name popins'>에어포스</div>
                                <div className='buy-btn-wrap'>
                                    <button className='btn-frame white-line'>Buy Now</button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default Section06