import React, { useEffect, useRef } from 'react';
import { gsap } from "gsap";
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import './section01.css'
import airMax from '../../../assets/images/section/airmax.png'
import nikeAir from '../../../assets/images/section/sec01-img02.png'
import nikecap from '../../../assets/images/section/sec01-img03.png'
import airpos from '../../../assets/images/section/airpos.png'
gsap.registerPlugin(ScrollTrigger);

const Section01 = () => {
    const packgeEboxRef = useRef(null);
    const packgeEboxRef1 = useRef(null);
    const copyboxRef = useRef(null);

    useEffect(() => {
        const packge = packgeEboxRef.current
        const packge1 = packgeEboxRef1.current
        const copy = copyboxRef.current;
        const tl = gsap.timeline({ defaults: { ease: 'power2.out' } });
        const t2 = gsap.timeline({ defaults: { ease: 'power2.out' } });
        const t3 = gsap.timeline({ defaults: { ease: 'power2.out' } });

        tl.fromTo(
            copy,
            { autoAlpha: 0, y: 150, transformOrigin: 'center' },
            {
                autoAlpha: 1,
                y: 0,
                duration: 0.25,
                ease: 'power2.out',
                scrollTrigger: {
                    trigger: copy,
                    start: "+=20 95%",
                    end: "+=20 60%",
                    scrub: 2,
                    invalidateOnRefresh: true,
                    // markers: {
                    //     startColor: '#ffd632',
                    //     endColor: 'black',
                    //     fontSize: '2rem',
                    //     indent: 200
                    // }
                },
            }
        );
        t2.fromTo(
            packge,
            { autoAlpha: 0, y: 150, transformOrigin: 'center' },
            {
                autoAlpha: 1,
                y: 0,
                duration: 0.15,
                ease: 'power2.out',
                scrollTrigger: {
                    trigger: packge,
                    start: "+=20 95%",
                    end: "+=20 60%",
                    scrub: 3,
                    invalidateOnRefresh: true,
                    // markers: {
                    //     startColor: '#ffd632',
                    //     endColor: 'black',
                    //     fontSize: '2rem',
                    //     indent: 200
                    // }
                },
            }
        );
        t3.fromTo(
            packge1,
            { autoAlpha: 0, y: 150, transformOrigin: 'center' },
            {
                autoAlpha: 1,
                y: 0,
                duration: 0.15,
                ease: 'power2.out',
                scrollTrigger: {
                    trigger: packge1,
                    start: "+=20 95%",
                    end: "+=20 60%",
                    scrub: 3,
                    invalidateOnRefresh: true,
                    // markers: {
                    //     startColor: '#ffd632',
                    //     endColor: 'black',
                    //     fontSize: '2rem',
                    //     indent: 200
                    // }
                },
            }
        );
    },[])

    return(
    <section className='section1'>
            <div className='inner packge-ani' ref={packgeEboxRef}> 
                <div className="flex-wrapper">
                    <div className="flex-box item01">
                        <div className="title-wrap" ref={copyboxRef}>
                            <h2>package</h2>
                            <p>
                                To make the warm gift even more impressive,<br/>
                                the gift collection is available
                            </p>
                        </div>
                        <div className="air-max-item">
                            <div className='air-max-img'>
                                <img src={airMax} alt=''/>
                            </div>
                            <div className='item01-infor-wrap'>
                                <h3 className='en'>Air Max 280</h3>
                                <p>
                                    Adds savory flavor to the savory taste potatoes<br/>
                                    Original dough Crunch rice preserves 
                                </p>
                            </div>
                        </div>
                    </div>
                    <div className='flex-box-inner'>
                        <div className="flex-box item02" style={{backgroundImage: `url(${nikeAir})`}}>
                            <div className='item02-infor-wrap'>
                                <h3 className='en'>NIKEAIR</h3>
                                <p>
                                    Adds savory flavor to the savory taste potatoes<br/>
                                    Original dough Crunch rice preserves 
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="flex-wrapper packge-ani" ref={packgeEboxRef1}>
                    <div className='nike-intro'>
                        따뜻한 선물이 더 큰 감동이 되도록, 기프트 컬렉션 15만원 이상 구매한 멤버 대상으로<br />
                        추첨을 통해 홀리데이 짐색과 특별한 슈박스 오너먼트를 증정합니다.<br />
                        홀리데이 짐색에 선물을 담아 마음을 전해보세요.
                    </div>
                    <div className='flex-box03-inner'>
                        <div className="flex-box item03" style={{ backgroundImage: `url(${nikecap})` }}>
                            <div className='item02-infor-wrap'>
                                <h3 className='en'>NIKCLUB</h3>
                                <p>
                                    Adds savory flavor to the savory taste potatoes<br />
                                    Original dough Crunch rice preserves
                                </p>
                            </div>
                        </div>
                    </div>
                    <div className="air-pos-item">
                        <div className='air-pos-img'>
                            <img src={airpos} alt='' />
                        </div>
                        <div className='item04-infor-wrap'>
                            <h3 className='en'>Air Pos</h3>
                            <p>
                                Adds savory flavor to the savory taste<br />
                                Original dough Crunch
                            </p>
                        </div>
                    </div>
                </div>
        </div>
    </section>
    )
}

export default Section01;