import React, { useEffect, useRef } from 'react';
import { gsap } from "gsap";
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Pagination, Autoplay } from 'swiper/modules';
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/autoplay';
import './section04.css'
import { Link } from 'react-router-dom';
gsap.registerPlugin(ScrollTrigger);

const Section04 = () => {
    const copyboxRef = useRef(null);
    const bestApparelboxRef = useRef(null);

    useEffect(() => {
        const copy = copyboxRef.current;
        const best = bestApparelboxRef.current
        const tl = gsap.timeline({ defaults: { ease: 'power2.out' } });
        const t2 = gsap.timeline({ defaults: { ease: 'power2.out' } });


        tl.fromTo(
            best,
            { autoAlpha: 0, y: 250, transformOrigin: 'center' },
            {
                autoAlpha: 1,
                y: 0,
                duration: 0.3,
                ease: 'power2.out',
                scrollTrigger: {
                    trigger: best,
                    start: "+=20 90%",
                    end: "+=20 60%",
                    scrub: 2,
                    invalidateOnRefresh: true,
                    // markers: {
                    //     startColor: '#ffd632',
                    //     endColor: 'black',
                    //     fontSize: '2rem',
                    //     indent: 200
                    // }
                },
            }
        );
        t2.fromTo(
            copy,
            { autoAlpha: 0, y: 150, transformOrigin: 'center' },
            {
                autoAlpha: 1,
                y: 0,
                duration: 0.25,
                ease: 'power2.out',
                scrollTrigger: {
                    trigger: copy,
                    start: "+=20 95%",
                    end: "+=20 60%",
                    scrub: 2,
                    invalidateOnRefresh: true,
                    // markers: {
                    //     startColor: '#ffd632',
                    //     endColor: 'black',
                    //     fontSize: '2rem',
                    //     indent: 200
                    // }
                },
            }
        );
    }, [])

    return (
        <section className='section1'>
            <div className='inner'>
                <div className="con-title" ref={copyboxRef}>
                    <h2>BEST<br />APPAREL</h2>
                    <p>
                        To make the warm gift even more impressive.
                    </p>
                </div>
            </div>
            <div className='best-inner' ref={bestApparelboxRef}>
                <div className="best-swiper">
                    <Swiper
                        slidesPerView={3}
                        loop={true}
                        centeredSlides={true}
                        pagination={{
                            el: '.best-swiper-pagination',
                            type: 'progressbar',
                        }}
                        modules={[Pagination, Autoplay]}
                    // autoplay={{ delay: 3500, disableOnInteraction: false }}
                    >
                        <SwiperSlide>
                            <Link to={'/'}>
                                <div className="best-product">
                                    <div className="best-img">
                                        <div className="img-box" style={{ backgroundImage: `url(${require('../../../assets/images/main/best-prudect01.png')})` }}></div>
                                    </div>
                                    <div className="product-info">
                                        <strong>나이키 스우시 경량 다운 재킷</strong>
                                        <p>주니어 재킷</p>
                                        <div className="product-price">
                                            <span>10%</span><span>185,000원</span><span>227,600원</span>
                                        </div>
                                    </div>
                                </div>
                            </Link>
                        </SwiperSlide>
                        <SwiperSlide>
                            <Link to={'/'}>
                                <div className="best-product">
                                    <div className="best-img">
                                        <div className="img-box" style={{ backgroundImage: `url(${require('../../../assets/images/main/best-prudect02.png')})` }}></div>
                                    </div>
                                    <div className="product-info">
                                        <strong>나이키 스포츠웨어 솔로 스우시</strong>
                                        <p>여성 카라 하이파일 재킷</p>
                                        <div className="product-price">
                                            <span>10%</span><span>185,000원</span><span>227,600원</span>
                                        </div>
                                    </div>
                                </div>
                            </Link>
                        </SwiperSlide>
                        <SwiperSlide>
                            <Link to={'/'}>
                                <div className="best-product">
                                    <div className="best-img">
                                        <div className="img-box" style={{ backgroundImage: `url(${require('../../../assets/images/main/best-prudect03.png')})` }}></div>
                                    </div>
                                    <div className="product-info">
                                        <strong>나이키 스포츠웨어 하이 신필 파카</strong>
                                        <p>남성 풀집 리버서블 재킷</p>
                                        <div className="product-price">
                                            <span>10%</span><span>185,000원</span><span>227,600원</span>
                                        </div>
                                    </div>
                                </div>
                            </Link>
                        </SwiperSlide>
                        <SwiperSlide>
                            <Link to={'/'}>
                                <div className="best-product">
                                    <div className="best-img">
                                        <div className="img-box" style={{ backgroundImage: `url(${require('../../../assets/images/main/best-prudect04.png')})` }}></div>
                                    </div>
                                    <div className="product-info">
                                        <strong>나이키 스포츠웨어 하이 신필 파카</strong>
                                        <p>남성 풀집 리버서블 재킷</p>
                                        <div className="product-price">
                                            <span>10%</span><span>185,000원</span><span>227,600원</span>
                                        </div>
                                    </div>
                                </div>
                            </Link>
                        </SwiperSlide>
                    </Swiper>
                    <div className="pagnation-box">
                        <div className='best-swiper-pagination'></div>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default Section04;